import React from 'react';
import PropTypes from 'prop-types';

import { StyledHead } from 'components/Train/StyledTrain';

const Head = ({ type }) => (
  <StyledHead>
    {type === 'details' ? (
      <>
        <li>Imi</li>
        <li>Nazwisko</li>
      </>
    ) : (
      <li>Grupa</li>
    )}
    <li>OSRG 1</li>
    <li>Doowe 1</li>
    <li>Powierzchnia 1</li>
    <li>OSRG 2</li>
    <li>Doowe 2</li>
    <li>Powierzchnia 2</li>
    {type === 'read' ? <li>Szczegy</li> : null}
    {type === 'create' ? <li>Opcje</li> : null}
    {type === 'details' ? <li>Szczegy</li> : null}
  </StyledHead>
);

Head.propTypes = {
  type: PropTypes.string,
};

export default Head;
